.header{
    height: 60px;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: #131921;
}

.header_logo{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}

.header_search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}

.header_searchInput{
    height: 15px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header_searchIcon{
    padding: 5px;
    height: 22px;
    background-color: #cd9042;
}

.header_options{
    display: flex;
    flex-direction: column;
    margin-left:10px;
    margin-right:10px;
    color: white;

}

.header_nav{
    display: flex;
    justify-content: space-evenly;
}

.header_optionLineOne{
    font-size: 10px;

}

.header_optionLineTwo{
    font-size: 13px;
    font-weight: 700;
}

.header_optionBasket{
    display: flex;
    align-items: center;
    color: white;
}

.header_basketCount{
    margin-left: 10px;
    margin-right: 10px;
}